import {LoadingController, ModalController} from 'ionic-angular';
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { EmailAttachViewModel } from "../../Model/ViewModel/EmailAttachViewModel";

/**
 * Generated class for the EmailAttachModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-email-attach-add-edit-modal",
  templateUrl: "email-attach-add-edit-modal.html"
})
export class EmailAttachAddEditModalPage {
  item: any;
  mode: string;
  CanEditEmail: boolean;
  title:string;
  EmailAttach_type:string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
    public modalCtrl: ModalController,

  ) {
    this.item = new EmailAttachViewModel();
    this.item.exec_group=navParams.data.item.exec_group;
    this.item.exec_status_key=navParams.data.item.exec_status_key;
    this.item.attach_exec_group=navParams.data.item.attach_exec_group;
    this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;
    this.item.email_id=navParams.data.item.email_id;


    this.CanEditEmail = navParams.data.CanEditEmail;
    this.mode = navParams.data.mode;
    this.title="Email附件設定";
  }
  SelectBatchExecProgram() {
    let modal = this.modalCtrl.create("BatchExecProgramSelectModalPage", {
      select_item: "attach_filelist_"+ this.item.attach_exec_group,
      ssis_program_set_no:"attach_filelist",
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;

      this.item.attach_exec_group = select_data.exec_group;
    });

    modal.present();
  }
  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad EmailAttachModalPage");
  }
}



